.Add {
    text-align: center;
    width: 350px;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: calc(10px + 2vmin);
    color: #424647;
}
.control {
  width:280px;
  background-color: #F2F5FA;
  padding: 10% 5% 10% 5% ;
  margin: 20px 0px 20px 0px;
}
.sum-text {
  font-size: large;
  padding: 4px 0px 4px 0px;
  margin: 0px 0px 8px 0px;
}
.sum-text-red {
  font-size: large;
  padding: 4px 0px 4px 0px;
  margin: 0px 0px 8px 0px;
  color: #ff7979;
}
.control-name {
  text-align: left;
  font-size: large;
  font-weight: bold;
}
.control-point {
  text-align: right;
  font-size: medium;
  font-weight: bold;
}
.player-control {
  margin-bottom: 5px;
}
.adjust {
  width: 45px;
  height: 45px;
  border-radius: 8px;
  font-size: larger;
  text-align: center;
  border: none;
  padding: 0px;
}
.number {
  margin: 0px 20px 0px 20px;
  width: 170px;
  height: 37px;
  border-radius: 4px;
  border: none;
  background-color: #394745;
  text-align: center;
  font-size: larger;
}
.red-number {
  margin: 0px 20px 0px 20px;
  width: 170px;
  height: 37px;
  border-radius: 4px;
  border: none;
  background-color: #394745;
  text-align: center;
  font-size: larger;
  color: #ff7979;
}

.submit{
  font-size: larger;
  width: 150px;
  height: 45px;
  margin: 8px 0px 30px ;
  border-radius: 8px;
  border: none;
  background-color: #424647;
  color: #E5E5E5;
}
.playerName {
  /* display: none; */
  padding: 0px;
  margin: 0px;
  font-size: large;
  font-weight: bold;
}